
Template.body.addContent((function() {
  var view = this;
  return [ Spacebars.include(view.lookupTemplate("image_add_form")), "\n  \n  ", HTML.NAV({
    "class": "navbar navbar-default navbar-fixed-top"
  }, "\n    ", HTML.DIV({
    "class": "container"
  }, "\n      ", Spacebars.include(view.lookupTemplate("loginButtons")), "\n    "), "\n  "), "\n  \n  ", HTML.DIV({
    "class": "container"
  }, "\n    ", HTML.H1("Welcome to Image Share ", Blaze.View("lookup:username", function() {
    return Spacebars.mustache(view.lookup("username"));
  })), "\n    ", Spacebars.include(view.lookupTemplate("images")), "\n  ") ];
}));
Meteor.startup(Template.body.renderToDocument);

Template.__checkName("images");
Template["images"] = new Template("Template.images", (function() {
  var view = this;
  return [ Blaze.If(function() {
    return Spacebars.call(view.lookup("currentUser"));
  }, function() {
    return [ "\n    ", HTML.BUTTON({
      "class": "btn btn-success js-show-image-form"
    }, "Add Image"), "\n  " ];
  }), "\n  \n  ", HTML.H2("\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("filtering_images"));
  }, function() {
    return [ "\n      Showing images by user ", Blaze.View("lookup:getFilterUser", function() {
      return Spacebars.mustache(view.lookup("getFilterUser"));
    }), " ", HTML.A({
      href: "#",
      "class": "js-unset-image-filter"
    }, "Show all images"), "\n    " ];
  }), "\n  "), "\n  \n  \n  ", HTML.DIV({
    "class": "row"
  }, "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("images"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "col-xs-12 col-md-3",
      id: function() {
        return Spacebars.mustache(view.lookup("_id"));
      }
    }, "\n      ", HTML.DIV({
      "class": "thumbnail"
    }, "\n        \n          ", HTML.IMG({
      "class": "js-images thumbnail-img",
      src: function() {
        return Spacebars.mustache(view.lookup("img_src"));
      },
      alt: function() {
        return Spacebars.mustache(view.lookup("img_alt"));
      }
    }), "\n          \n          ", HTML.DIV({
      "class": "caption"
    }, "\n              ", HTML.H3("Rating: ", Blaze.View("lookup:rating", function() {
      return Spacebars.mustache(view.lookup("rating"));
    })), "\n              ", HTML.P(Blaze.View("lookup:img_alt", function() {
      return Spacebars.mustache(view.lookup("img_alt"));
    })), "\n              ", HTML.P("User: \n                ", HTML.A({
      href: "#",
      "class": "js-set-image-filter"
    }, Blaze.View("lookup:getUser", function() {
      return Spacebars.mustache(view.lookup("getUser"), view.lookup("createdBy"));
    })), "\n              "), "\n              \n              ", HTML.P("\n                ", Blaze._TemplateWith(function() {
      return {
        mutable: Spacebars.call(true),
        "class": Spacebars.call("js-rate-image"),
        id: Spacebars.call(view.lookup("_id"))
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("starsRating"));
    }), "\n              "), "\n              \n              ", HTML.BUTTON({
      "class": "js-del-images btn btn-warning"
    }, "Delete"), "\n          "), "\n          \n       "), "\n     "), " ", HTML.Comment(" /col "), "\n    " ];
  }), "\n   "), HTML.Raw(" <!-- /row -->") ];
}));

Template.__checkName("image_add_form");
Template["image_add_form"] = new Template("Template.image_add_form", (function() {
  var view = this;
  return HTML.Raw('<div class="modal fade" id="image_add_form">\n    <div class="modal-dialog">\n      <div class="modal-content">\n        <div class="modal-header">\n          <div class="modal-title">\n          </div>\n        </div>\n        <div class="modal-body">\n          <form class="js-add-image">\n            <input type="text" name="img_src">\n            <br><input type="text" name="img_alt">\n            <button class="btn btn-success">Save</button>\n          </form>\n        </div>\n        <div class="modal-footer">\n          <button class="btn btn-warning" data-dismiss="modal">Cancel</button>\n        </div>\n      </div>\n    </div>\n  </div>');
}));
